import { ChangeEvent, SetStateAction } from "react"
import { OrganizationMembershipResource, UserResource } from "@clerk/types"
import { FileRowType } from "@rp/common/utils"
import { SelectedFile } from "../Files"
import { OwnerNameData } from "../files.types"

type HandleCheckboxChangeFuncProps = {
  ev: ChangeEvent<HTMLInputElement>
  val: boolean
  setSelectedFiles: (value: SetStateAction<Array<SelectedFile>>) => void
  completedFiltered: Array<FileRowType>
  shared: boolean
}

const handleCheckboxChangeFunc = ({
  ev,
  val,
  setSelectedFiles,
  completedFiltered,
  shared,
}: HandleCheckboxChangeFuncProps) => {
  ev.stopPropagation()
  if (val) {
    setSelectedFiles(completedFiltered.map(f => ({ id: f.id, shared })))
  } else {
    setSelectedFiles([])
  }
}

const getOwnerNameData = (
  user: UserResource | null | undefined,
  orgUser: OrganizationMembershipResource | undefined,
): OwnerNameData => {
  if (!orgUser || !user) return { ownerName: undefined, ownerDisplayName: undefined }

  const ownerName = `${orgUser.publicUserData.firstName} ${orgUser.publicUserData.lastName}`
  const ownerDisplayName = orgUser.publicUserData.userId === user.id ? "You" : ownerName

  return { ownerName, ownerDisplayName }
}

export { handleCheckboxChangeFunc, getOwnerNameData }
